/**
 * Layout component that queries for data
 * with Gatsby's StaticQuery component
 *
 * See: https://www.gatsbyjs.org/docs/static-query/
 */

import React from "react"
import PropTypes from "prop-types"
import { Link } from "gatsby"
import { StaticQuery, graphql } from "gatsby"
import classNames from "classnames"

import Row from "./Row"
import Column from "./Column"
import Icon from "./Icon"

import "../css/Header.css"

const Header = ({ description }) => (
  <StaticQuery
    query={graphql`
      query SiteTitleQuery {
        site {
          siteMetadata {
            title
            description
            menuLinks {
              name
              link
            }
          }
        }
      }
    `}
    render={data => (
      <header>
        <Row>
          <Link className={classNames( 'icon' )} to='/' >
            <Icon />
          </Link>
          <Row>
            <Column>
              <h className={classNames( 'title' )}>
                <Link className={classNames( 'contrastFontColor' )} to="/">
                  {data.site.siteMetadata.title}
                </Link>
              </h>
            </Column>
            <Column>
              <h className={classNames( 'description', 'contrastFontColor' )}>
                {description ? description : data.site.siteMetadata.description}
              </h>
            </Column>
          </Row>
        </Row>
        <nav>
          {
            data.site.siteMetadata.menuLinks.map(link =>
              <li key={link.name} className={classNames( 'menuEntry' )}>
                <Link className={classNames( 'contrastFontColor' )} to={link.link}>
                  {link.name}
                </Link>
              </li>)
          }
        </nav>
      </header>
    )}
  />
)

Header.propTypes = {
  description: PropTypes.string,
}

Header.defaultProps = {
  description: "",
}

export default Header
